.navBar {
  position: relative;
  z-index: 1;
  height: 10vh;
  box-shadow: 0 0 10px rgb(0,0,0);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.navButton {
  border-radius: 12px;
  background-color: #ffffff;
  width: 100px;
  height:20px;
  padding: 5px;
  margin-inline: 10px;
  align-content: center
};    
